import Axios from 'axios';
import React, { Component } from 'react'
import { Link, withRouter } from 'react-router-dom';
import Footer from '../layout/Footer'
import Header from '../layout/Header'
class Order extends Component {
    constructor(props) {
        super(props);
        this.state = {
            dt: [],
            type:5
        }
    }

    formatMoney(t) {
        return t.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
    }
    componentDidMount() {
        window.scrollTo(0, 0)
      }
    componentWillMount() {
        if (sessionStorage.getItem('userID') === null) {
            alert('Vui lng ng nhp !!!')
            this.props.history.push('/index')
        }
        Axios.get('/order')
            .then(res => {
                var dt = res.data.filter(x => x.userid === sessionStorage.getItem('userID'));
                
                this.setState({
                    dt: dt
                })
            })
    }
    clickCancel = (id, item) => {
        Axios.post("/order/cancel", {
            id: id,
            item: item
        })
            .then(res => {
                alert(res.data.mess);
                Axios.get('/order')
                    .then(res => {
                        var dt = res.data.filter(x => x.userid === sessionStorage.getItem('userID'));
                        console.log(dt);
                        this.setState({
                            dt: dt
                        })

                    })
            })
    }
    
    clickAddCart = (item) => {
        Axios.post('/cart/again', {
            item: item,
            userid: sessionStorage.getItem('userID')
        })
            .then(res => {
                alert(res.data.mess);
            })
    }
    to_slug(str) {
        // Chuyn ht sang ch thng
        str = str.toLowerCase();

        // xa du
        str = str.replace(/(||||||||||||||||)/g, 'a');
        str = str.replace(/(||||||||||)/g, 'e');
        str = str.replace(/(||||)/g, 'i');
        str = str.replace(/(||||||||||||||||)/g, 'o');
        str = str.replace(/(||||||||||)/g, 'u');
        str = str.replace(/(||||)/g, 'y');
        str = str.replace(/()/g, 'd');

        // Xa k t c bit
        str = str.replace(/([^0-9a-z-\s])/g, '');

        // Xa khong trng thay bng k t -
        str = str.replace(/(\s+)/g, '-');

        // xa phn d -  u
        str = str.replace(/^-+/g, '');

        // xa phn d -  cui
        str = str.replace(/-+$/g, '');

        // return
        return str;
    }
    formAll() {
        var dt;
        if(this.state.type!==5){

            dt=this.state.dt.filter(x=>x.status===this.state.type);
        }
        else{
           dt=this.state.dt;
        }
        if(dt.length===0){
            return <img src="/emty.svg" height="300" width="100%" alt="Hnh"/>
        }
else{
        return dt.map((x, key) =>
            <div key={key} className="bg-white shadow item rounded">
                <div className="d-flex justify-content-between">
                {x.dateca!==undefined?<div className=" pl-4">Ngy hy :<div className="text-danger ">{x.datecan}</div></div>:
                x.datecom!==undefined?<div className=" pl-4">Ngy bn giao :<div className="text-danger ">{x.datecom}</div></div>:
                x.dateget!==undefined?<div className=" pl-4">Ngy nhn :<div className="text-danger ">{x.dateget}</div></div>:
                <div className=" pl-4">Ngy t :<div className="text-danger ">{x.datelc}</div></div>
            }
                    
                    <div className="pr-3">Trng thi :<div className="text-danger pl-1">{x.status === 1 ? "Ch nhn" : x.status === 2 ? ' nhn' :
                        x.status === 3 ? ' bn giao' : x.status === 4 ? 'Hon thnh' : ' hy'} </div></div>
                </div>

                {x.item.map((y, k) =>
                    <Link to={"/chi-tiet/" + this.to_slug(y.name) + "/" + y.productid + ".html"} className={"tabs tab-"+k} key={k}>
                        <hr />
                        <div className="d-flex justify-content-between">
                            <div className="row order-content">
                                <div className="px-4">
                                    <img className="ml-4" src={y.img} alt="Hnh" width='60' />
                                </div>
                                <div className="px-md-4 text-order py-lg-3">
                                    <div className="h4 name-pro">{y.name}</div>
                                    <div>x {y.qty}</div>
                                </div>
                            </div>
                            <div className="px-4 order-type py-lg-3">
                                {y.typeorder === '1' ?
                                    <div>Mua</div> : y.typeorder === '0.7' ?
                                        <div>Thu 7 ngy</div> : y.typeorder === '0.5' ?
                                            <div>Thu 3 ngy</div> : <div>Thu 1 ngy</div>}
                                <div> {this.formatMoney(y.price)} VND</div>
                            </div>
                        </div>
                        <hr />
                    </Link>
                )}
                <div className="d-flex justify-content-end">
                    Tng s tin :<div className="text-danger px-3 font-weight-bold"> {this.formatMoney(x.total)} VND</div>
                </div>
                <div className="d-flex justify-content-end">
                    {x.status === 1 && <button type="button" onClick={() => this.clickCancel(x._id, x.item)} className="btn btn-danger">Hy</button>}
                    {/* {x.status === 0 && <button type="button" onClick={() => this.clickAddCart(x.item)} className="btn btn-danger">Thm gi hng</button>} */}
                    {/* {x.status === 3 && <button type="button" onClick={() => this.clickComplete(x._id)} className="btn btn-primary">Xc nhn  nhn c hng</button>} */}
                    {/* <button type="button" className="btn btn-primary"> nhn</button> */}
                </div>

                <hr />
            </div>

        )
    }
    }
    changeType=(id)=>{
       this.setState({
           type:id
       })
    }
    render() {
        return (
            <div >
                <Header  />
                <div  className="content-chitiet">

                </div>
                <div className="pt-1">

                </div>
                {/* <Boxicon /> */}
                <div className="container-md">
                    <hr />

                    <div className="d-flex justify-content-around bg-white shadow py-3 mb-5">
                        <div className="form-check-inline br">
                            <label className="form-check-label">
                                <input type="radio" onChange={()=>this.changeType(5)} className="form-check-input d-none  typeshow " name="typeshow" />                                <div className="typetext">Tt c</div>
    </label>
                        </div>
                        <div className="form-check-inline br">
                            <label className="form-check-label">
                                <input type="radio" onChange={()=>this.changeType(1)} className="form-check-input d-none  typeshow" name="typeshow" />
                                <div className="typetext">Ch bn n nhn</div>
    </label>
                        </div>
                        <div className="form-check-inline br">
                            <label className="form-check-label">
                                <input type="radio" onChange={()=>this.changeType(2)} className="form-check-input d-none  typeshow" name="typeshow" />
                                <div className="typetext"> nhn</div>
    </label>
                        </div>
                        <div className="form-check-inline br">
                            <label className="form-check-label">
                                <input type="radio" onChange={()=>this.changeType(3)} className="form-check-input d-none  typeshow" name="typeshow" />
                                <div className="typetext"> bn giao</div>
    </label>
                        </div>
                        <div className="form-check-inline br">
                            <label className="form-check-label">
                                <input type="radio" onChange={()=>this.changeType(0)} className="form-check-input d-none  typeshow" name="typeshow" />
                                <div className="typetext"> hy</div>
    </label>
                        </div>
                    </div>

                    {this.formAll()}

                    {/* <div className="d-flex justify-content-around">
                        <Link to="/u/order">Tt c</Link>
                        <Link to="/ok">ang giao</Link>
                        <Link to="/u/order/type=3"> giao</Link>
                        <Link to="/u/order/type=4"> hy</Link>
                    </div> */}
                    {/* <Switch>
                        <Route path="/u/order.html" exact>
                            <Redirect to="/u/order/type=1" />
                        </Route>
                        <Route path="/u/order/3" exact>
                            <contentOrder />
                        </Route>
                        <Route path="/ok">
                            <contentOrder />
                        </Route>
                        <Route path="/u/order">
                           
                        </Route>
                        <Route path="/u/order/type=4">
                            <div>Hello</div>
                        </Route>
                    </Switch> */}
                </div>
                <Footer />

            </div>
        )
    }
}
export default withRouter(Order)